<template>

    <v-container fluid>    
  <div>Details: {{$route.params.id}}</div>
  <v-card>
      <v-card-title>
          {{product.title}}
      </v-card-title>

      <v-card-text>
          <strong>
              author: {{product.owner}}      createdAt:{{product.createdAt}}
          </strong>
          <v-img :src="product.image"></v-img>
          <p v-html="product.description"></p>
      </v-card-text>
  </v-card>
  </v-container>
</template>

<script>
import product from "@/api/service/product.js"; // 导入商品模块
export default {
    name:'ProductDetails',
    data:()=>({
        product:"",
    }),
    mounted(){
        console.log("get get get ")
        this.getProductById(this.$route.params.id);
    },
    methods:{
        getProductById(id){
            console.log("get a product")
            product
            .getProductById(id)
            .then(item => {
                this.product = {
                    id: item.id,
                    title: item.get("title"), // item.get('')用于获取指定字段的值
                    price: item.get("price"),
                    description: item.get("description"),
                    image: item.get("image").get("url"),
                    owner: item.get("owner") ? item.get("owner").get("username") : "",
                    createdAt: item.createdAt,
                };
            })
            .catch(error=>{})
        }
    }
}
</script>

